.navigation {
  color: rgb(236, 170, 104);
  background-color: rgba(0, 0, 0, 0.495);
  padding: 10px;
  width: 100%;
  font-size: 1.4em;
  #nav {
    display: flex;
    justify-content: space-around;
  }
  a {
    color: rgb(245, 245, 245);
  }
  .nav-active {
    color: rgb(248, 146, 44);
  }
  ul:hover li:not(:hover) {
    opacity: 1;
  }
  ul:hover li:hover {
    color: rgb(255, 254, 252);
  }

  .nav-portfolio {
    cursor: pointer;
    display: flex;
    transition: 0.5s;

    &:hover .nav-projects,
    .nav-projects:hover {
      visibility: visible;
      opacity: 1;

      li {
        transform: rotatey(0deg);
      }
    }

    .nav-projects {
      position: absolute;
      left: 33%;
      top: 6%;
      visibility: hidden;
      opacity: 0;
      padding: 0 40px;
      width: 300px;
      transform: translateX(75px);
      transition: 0.3s;
      perspective: 500px;
      @media screen and(max-width: 450px) {
        left: 10%;
      }
      @media screen and(min-width: 1300px) {
        left: 38%;
      }
      @media screen and(min-width: 2300px) {
        left: 43%;
        top: 4%;
      }

      li {
        transform: rotatey(30deg) scaleX(1.2);
        transform-origin: center;
        color: rgb(248, 146, 44);

        transition: 0.3s;
      }
    }
  }
}
