.social-network {
  position: absolute;
  right: 1rem;
  bottom: 0;
  width: 20%;
  font-size: 2rem;
  a {
    color: whitesmoke;
  }

  @media screen and (max-width: 650px) {
    width: 30%;
  }
  @media screen and(max-width: 450px) {
    right: 10%;
  }
  ul {
    display: flex;
    width: 100%;
    min-width: 100px;
    justify-content: space-around;

    @media screen and (max-width: 450px) {
      min-width: 100%;
    }

    a {
      padding: 1rem;
      transition: 1.1s ease-out;
    }
  }
}
