.home {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  background: url("../../../public/images/bg.jpg") center/cover;
  .main-content {
    height: 80vh;
    animation: animate 2s forwards cubic-bezier(0.5, 1, 0.89, 1);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h1 {
      text-align: center;
      font-size: 6em;
      font-weight: bold;
      text-transform: uppercase;
      font-style: italic;
      color: white;
      opacity: 0;
      filter: blur(4px);
      animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
      @media screen and(max-width: 800px) {
        font-size: 4em;
      }
      @media screen and(max-width: 450px) {
        font-size: 3em;
      }
    }
    h2 {
      text-align: center;
      font-size: 2.7em;
      color: whitesmoke;
      font-style: italic;
      opacity: 0;
      filter: blur(4px);
      animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
      @media screen and(max-width: 800px) {
        font-size: 2em;
      }
    }
  }
}
@keyframes animate {
  100% {
    transform: scale(1.1);
  }
}
@keyframes fade-in {
  100% {
    opacity: 1;
    filter: blur(0);
  }
}
