.producers-container {
  min-height: 200vh;
  background: url("../../../public/images/prod-bg.webp") center/cover fixed;
  .producers {
    color: whitesmoke;
    animation: animate 2s forwards cubic-bezier(0.5, 1, 0.89, 1);
    h1 {
      opacity: 0;
      filter: blur(4px);
      animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
      text-transform: uppercase;
      text-align: center;
      font-style: italic;
      font-size: 3em;
      margin-top: 70px;
    }
    p {
      opacity: 0;
      filter: blur(4px);
      animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
      margin: 40px auto;
      text-align: center;
      width: 70%;
      font-size: 1.2em;
    }
    .sections-container {
      background-color: rgba(245, 245, 245, 0.31);
      -webkit-box-shadow: 2px 1px 15px 7px #00000083;
      box-shadow: 2px 1px 15px 7px #0000007f;
      img {
        border-radius: 5px;
        cursor: pointer;
        transition: 0.3s;
        width: 30%;
        opacity: 0;
        filter: blur(4px);
        animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
        -webkit-box-shadow: 2px 1px 15px 7px #00000083;
        box-shadow: 2px 1px 15px 7px #0000007f;
        &:hover {
          transform: scale(1.05);
        }
      }
      .section-1 {
        padding: 40px;
        text-align: center;
        h3 {
          font-style: italic;
          font-size: 1.3em;
        }
        p {
          text-align: center;
          margin: 10px auto;
          width: 50%;
        }
        a {
          color: wheat;
          transition: 0.3s ease;
          font-size: 1.3em;
          font-weight: bold;
          text-transform: uppercase;
          padding-top: 15px;
          @media screen and(max-width: 800px) {
            font-size: 0.9em;
          }
          @media screen and(max-width: 550px) {
            font-size: 0.7em;
          }
          &:hover {
            color: rgb(53, 215, 53);
          }
        }
      }
      .section-2 {
        padding: 40px;
        text-align: center;
        h3 {
          font-style: italic;
          font-size: 1.3em;
        }
        p {
          text-align: center;
          margin: 10px auto;
          width: 50%;
        }
        a {
          color: wheat;
          transition: 0.3s ease;
          font-size: 1.3em;
          font-weight: bold;
          text-transform: uppercase;
          padding-top: 15px;
          @media screen and(max-width: 800px) {
            font-size: 0.9em;
          }
          @media screen and(max-width: 550px) {
            font-size: 0.7em;
          }
          &:hover {
            color: rgb(53, 215, 53);
          }
        }
      }
      .section-3 {
        padding: 40px;
        text-align: center;
        h3 {
          font-style: italic;
          font-size: 1.3em;
        }
        p {
          text-align: center;
          margin: 10px auto;
          width: 50%;
        }
        a {
          color: wheat;
          transition: 0.3s ease;
          font-size: 1.3em;
          font-weight: bold;
          text-transform: uppercase;
          padding-top: 15px;
          @media screen and(max-width: 800px) {
            font-size: 0.9em;
          }
          @media screen and(max-width: 550px) {
            font-size: 0.7em;
          }
          &:hover {
            color: rgb(53, 215, 53);
          }
        }
      }
    }
  }
}
@keyframes animate {
  100% {
    transform: scale(0.9);
  }
}
@keyframes fade-in {
  100% {
    opacity: 1;
    filter: blur(0);
  }
}
