h1 {
  text-align: center;
  margin: 70px auto 20px auto;
  text-transform: uppercase;
  font-style: italic;
  color: whitesmoke;
  opacity: 0;
  filter: blur(4px);
  animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
.about-container {
  min-height: 100vh;
  background: url("../../../public/images/vegetables-set-left-black-slate.jpg")
    center/cover;
  @media screen and (max-width: 1024px) {
    min-height: 170vh;
  }
  .container {
    animation: animate 2s forwards cubic-bezier(0.5, 1, 0.89, 1);

    margin: 0 auto;
    height: 100vh;
    display: flex;
    justify-content: center;

    position: relative;
    width: 1100px;
    flex-wrap: wrap;
    padding: 30px;
    @media screen and (max-width: 1024px) {
      max-width: 220px;
      display: block;
      margin-left: auto;
    }
    .card {
      opacity: 0;
      filter: blur(4px);
      animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
      width: 300px;
      height: 215px;
      background: #fff;
      margin: 30px 10px;
      padding: 20px 15px;
      display: flex;
      flex-direction: column;
      box-shadow: 0 5px 202px rgba(0, 0, 0, 0.5);
      transition: 0.3s ease-in-out;
      border-radius: 3px;
      @media screen and (max-width: 1024px) {
        transform: translateX(-20%);
      }

      .imgBx {
        position: relative;
        width: 260px;
        top: -60px;
        z-index: 1;
        left: 20px;
        box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
        img {
          width: 100%;
          height: 230px;
          border-radius: 3px;
          cursor: pointer;
        }
      }
      .content {
        position: relative;
        margin-top: -140px;
        padding: 10px 15px;
        text-align: center;
        color: #111;
        visibility: hidden;
        opacity: 0;
        transition: 0.3s ease-in-out;
        @media screen and (max-width: 1024px) {
          width: 300px;
        }
      }
      p {
        font-size: 0.9em;
      }
    }
    #one {
      @media screen and (max-width: 1024px) {
        width: 340px;
      }
      &:hover {
        height: 530px;
      }
    }
    #two {
      @media screen and (max-width: 1024px) {
        width: 340px;
      }
      &:hover {
        height: 450px;
      }
    }
    #three {
      @media screen and (max-width: 1024px) {
        width: 340px;
      }
      &:hover {
        height: 580px;
      }
    }
  }
  .logo {
    height: 110px;
    width: 110px;
    top: 10%;
    right: 0;
  }
  @keyframes animate {
    100% {
      transform: scale(0.95);
    }
  }
  @keyframes fade-in {
    100% {
      opacity: 1;
      filter: blur(0);
    }
  }
  .scroll-bottom {
    a {
      position: absolute;
      transform: translate(-50%, -50%);
      top: 50%;
      font-size: 6rem;
      color: rgb(255, 255, 255);
      transition: 0.25s;
      user-select: none;
      padding: 0 14px;

      &:hover {
        font-size: 4rem;
      }
    }

    .left {
      left: 15px;
    }

    .right {
      right: -24px;
    }
  }
}

.container .card:hover .content {
  visibility: visible;
  opacity: 1;
  margin-top: -40px;
  transition-delay: 0.3s;
}
